.hero{
    background-color: aqua;
    padding: 20px;
    margin: 20px;
    height: 800px;
    border: 3px solid red;
    border-radius: 20px;
    color: black;;
    box-shadow: 1px 1px 20px rgb(104, 104, 104);
}

.button{
    background-color: antiquewhite;
    border: 2px solid blue;
    border-radius: 5px;
    padding: 15px;
    font-size: 14px;
    font-weight: 700;
    margin-top: 10px;
    outline: none;
    margin: 15px;
    
}
.box{
    background-color: darkgrey;
    margin: 40px;
    padding: 20px;
    align-items: center;
    border: 3px solid blue;
    border-radius: 20px;
    text-align: center;
    box-shadow :  1px 1px 20px rgb(104, 104, 104);
}
